import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles, Box, withWidth, WithWidth, isWidthUp } from '@material-ui/core';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import scrollIntoView from 'scroll-into-view';
import { Link as RouterLink,RouteComponentProps,
	withRouter } from 'react-router-dom';
import Link from '@material-ui/core/Link';
import Ticker from 'react-ticker';
import { get } from 'lodash';
import LazyLoad from 'react-lazyload';
import Loadable from '@loadable/component';
import { connect } from 'react-redux';
import { DesktopTiles, HomePageLinks, Resources } from '../../constants/resources';
import Style from './style';
import { RootState } from '../../reducers';
const HomepageTile = Loadable(() => import('../../components/HomepageTile'));
const Contact = Loadable(() => import('../Contact'));
const Footer = Loadable(() => import('../../components/Footer'));
const ImageContent = Loadable(() => import('../../components/ImageContent'));
const Categories = Loadable(() => import('../Categories'));
interface Props extends WithStyles<typeof Style>, WithWidth, RouteComponentProps {
	[name: string]: any;
	isDesktopView: boolean;
}
/**
 * Home page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class Home extends React.PureComponent<Props> {
	componentDidMount() {
		// scrollIntoView(document.getElementById('home') as any);
		const {location} = this.props;
		const {hash} = location;
		if (hash !== '') {
			scrollIntoView(document.getElementById(hash), { align: { top: 0.09, left: 0 } })
		}
	}

	getLink = (item) => {
		const { classes } = this.props;
		const externalLink = get(item, 'externalLink', '');

		if (externalLink !== '') {
			return (
				<Link
					key={item.name}
					className={classes.homeLink}
					href={externalLink}
					target="__blank__"
					color="inherit"
					component="a"
				>
					{item.name} <ArrowForwardIcon />
				</Link>
			);
		}
		return (
			<Link
				key={item.name}
				className={classes.homeLink}
				to={item.link}
				color="inherit"
				component={RouterLink as any}
			>
				{item.name} <ArrowForwardIcon />
			</Link>
		);
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, isDesktopView } = this.props;
		const aboutLink = { link: '/about-us', externalLink: false, linkText: 'Know more' };
		const socialLink = { link: '/social-outreach', externalLink: false, linkText: 'Know more' };
		return (
			<div style={{ position: 'relative' }}>
				<Box height={38} className={classes.marqueeContainer} id="home">
					<Ticker height={38} offset={0} mode="smooth" direction="toLeft">
						{() => (
							<p className={classes.marquee}>
								Dear Customers - DMart does not issue any vouchers or coupons
								through any other website or social media platforms. All our
								promotions and offers are available only on the Dmart Ready App, our
								website{' '}
								<a href="https://www.dmart.in" target="__blank">
									www.dmart.in
								</a>{' '}
								and our Dmart Stores. &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
							</p>
						)}
					</Ticker>
				</Box>
				<div style={{ position: 'relative' }}>
					<LazyLoad height={300} offset={100}>
						<img
							src={isDesktopView ? '/images/home-desktop.png' : '/images/home.png'}
							alt="Home_hero_image"
							className={classes.heroImage}
						/>
					</LazyLoad>
					<div className={classes.linksContainer}>
						<div className={classes.tagline}>
							<div>
								{isDesktopView ? (
									<div
										className={clsx(classes.superLargeFont, classes.lineHeight)}
									>
										Unified By Value.
									</div>
								) : (
										<>
											<div
												className={clsx(
													classes.superLargeFont,
													classes.lineHeight
												)}
											>
												Unified
										</div>
											<div
												className={clsx(classes.largerFont, classes.lineHeight)}
											>
												By Value.
										</div>
										</>
									)}
							</div>
						</div>
						<div className={classes.links}>
							<div>
								<div className={classes.heroTexts}>
									<div className={clsx(classes.homeLinks, classes.largeFont)}>
										{HomePageLinks.map((item) => this.getLink(item))}
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				{isDesktopView && <div className={classes.aboutUsContainer}>
					<ImageContent content={<>{Resources.AboutUsPara_1}
					<br />
					<br />
					{Resources.AboutUsPara_2}
					<br />
					<br />
					{Resources.AboutUsPara_3}</>}
					heading="About" image="/images/store-desktop.png" link={aboutLink}
					padding="44px 32px"
					 />
					</div>}
				{isDesktopView && <HomepageTile items={DesktopTiles} />}
				{isDesktopView && <div className={classes.aboutUsContainer}>
					<ImageContent content={<>{Resources.SocialPara_1}
					<br />
					<br />
					{Resources.SocialPara_2}
					<br />
					<br />
					{Resources.SocialPara_3}</>}
					heading={Resources.SocialOutreachSubtitle} image="/images/social-desktop.png" link={socialLink}
					padding="44px 32px"
					leftContent />
					</div>}
				{isDesktopView && <><span id="#categories"></span><Categories /></>}
				{isDesktopView && <Contact />}
				{isDesktopView && <Footer />}
			</div>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	isDesktopView: state.screenReducer.isDesktopView
});

export default withStyles(Style, { name: 'DmartCorpHome' })(
	withWidth()(withRouter((connect(mapStateToProps)(Home))))
);
